export default {
	name: 'Newslist',
	props:{
		list:Array,
		urls: String
	},
	data() {
		return {
			goUrl: this.urls
		};
	},
	methods: {
		tap(o) {
			let _this = this;
			if (_this.goUrl != '') {
				uni.navigateTo({
					url: _this.goUrl + o
				});
			}
		}
	}
};
